import React, { useState } from 'react';
import { View, Text, TextInput, TouchableOpacity, StyleSheet } from 'react-native-web';

const MultiplayerControls = ({ 
  gameMode, 
  connectionStatus, 
  invitationCode, 
  error, 
  connectionData,
  waitingForAnswer,
  timeRemaining,
  onCreateGame, 
  onJoinGame, 
  onDisconnect, 
  onHandleAnswer,
  onCopyConnectionData,
  showMultiplayer,
  onToggleMultiplayer,
  gameStatus,
  onNewGame
}) => {
  const [hostConnectionData, setHostConnectionData] = useState('');
  const [guestAnswer, setGuestAnswer] = useState('');

  const handleJoinGame = () => {
    if (hostConnectionData.trim()) {
      onJoinGame(hostConnectionData.trim());
    }
  };

  const handleProcessAnswer = () => {
    if (guestAnswer.trim()) {
      onHandleAnswer(guestAnswer.trim());
    }
  };

  const getStatusColor = () => {
    switch (connectionStatus) {
      case 'connected': return '#4CAF50';
      case 'connecting': return '#FF9800';
      case 'error': return '#F44336';
      default: return '#9E9E9E';
    }
  };

  const getStatusText = () => {
    switch (connectionStatus) {
      case 'connected': return 'Connected';
      case 'connecting': return 'Connecting...';
      case 'error': return 'Connection Error';
      default: return 'Disconnected';
    }
  };

  const formatTime = (seconds) => {
    const minutes = Math.floor(seconds / 60);
    const remainingSeconds = seconds % 60;
    return `${minutes}:${remainingSeconds.toString().padStart(2, '0')}`;
  };

  if (gameMode === 'local' && !showMultiplayer) {
    return (
      <View style={styles.betaContainer}>
        <TouchableOpacity style={styles.betaLink} onPress={onToggleMultiplayer}>
          <Text style={styles.betaLinkText}> Try Online Multiplayer (BETA)</Text>
        </TouchableOpacity>
      </View>
    );
  }

  if (gameMode === 'local' && showMultiplayer) {
    return (
      <View style={styles.container}>
        <View style={styles.header}>
          <Text style={styles.title}>Play Online (BETA)</Text>
          <TouchableOpacity style={styles.closeButton} onPress={onToggleMultiplayer}>
            <Text style={styles.closeButtonText}></Text>
          </TouchableOpacity>
        </View>
        <Text style={styles.betaWarning}> Beta feature - Cross-device manual connection</Text>
        <View style={styles.buttonContainer}>
          <TouchableOpacity style={styles.button} onPress={onCreateGame}>
            <Text style={styles.buttonText}>Host Game</Text>
          </TouchableOpacity>
          <Text style={styles.orText}>or</Text>
          <View style={styles.joinContainer}>
            <TextInput
              style={styles.textArea}
              placeholder="Paste host's connection data here..."
              value={hostConnectionData}
              onChangeText={setHostConnectionData}
              multiline
              numberOfLines={3}
            />
            <TouchableOpacity 
              style={[styles.button, styles.joinButton]} 
              onPress={handleJoinGame}
              disabled={!hostConnectionData.trim()}
            >
              <Text style={styles.buttonText}>Join Game</Text>
            </TouchableOpacity>
          </View>
        </View>
      </View>
    );
  }

  return (
    <View style={styles.container}>
      <View style={styles.activeHeader}>
        <Text style={styles.title}>
          {gameMode === 'host' ? 'Hosting Game' : 'Joining Game'}
        </Text>
        <View style={styles.statusContainer}>
          <View style={[styles.statusDot, { backgroundColor: getStatusColor() }]} />
          <Text style={styles.statusText}>{getStatusText()}</Text>
          {connectionStatus === 'connecting' && timeRemaining > 0 ? (
            <Text style={styles.timerText}>{formatTime(timeRemaining)}</Text>
          ) : null}
        </View>
      </View>

      {gameMode === 'host' && connectionData && connectionStatus !== 'connected' ? (
        <View style={styles.invitationContainer}>
          <Text style={styles.invitationLabel}>Step 1: Share this connection data:</Text>
          <View style={styles.dataContainer}>
            <TextInput
              style={styles.readOnlyTextArea}
              value={connectionData}
              multiline
              numberOfLines={4}
              editable={false}
            />
            <TouchableOpacity style={styles.copyButton} onPress={onCopyConnectionData}>
              <Text style={styles.copyButtonText}>Copy</Text>
            </TouchableOpacity>
          </View>
          <Text style={styles.instructionText}>
            Send this data to your friend via message/email
          </Text>
          
          {waitingForAnswer ? (
            <View style={styles.answerSection}>
              <Text style={styles.invitationLabel}>Step 2: Paste your friend's response:</Text>
              <View style={styles.dataContainer}>
                <TextInput
                  style={styles.textArea}
                  placeholder="Paste your friend's connection response here..."
                  value={guestAnswer}
                  onChangeText={setGuestAnswer}
                  multiline
                  numberOfLines={3}
                />
                <TouchableOpacity 
                  style={[styles.copyButton, styles.processButton]} 
                  onPress={handleProcessAnswer}
                  disabled={!guestAnswer.trim()}
                >
                  <Text style={styles.copyButtonText}>Connect</Text>
                </TouchableOpacity>
              </View>
            </View>
          ) : null}
        </View>
      ) : null}

      {gameMode === 'guest' && connectionData && connectionStatus !== 'connected' ? (
        <View style={styles.invitationContainer}>
          <Text style={styles.invitationLabel}>Send this response to the host:</Text>
          <View style={styles.dataContainer}>
            <TextInput
              style={styles.readOnlyTextArea}
              value={connectionData}
              multiline
              numberOfLines={4}
              editable={false}
            />
            <TouchableOpacity style={styles.copyButton} onPress={onCopyConnectionData}>
              <Text style={styles.copyButtonText}>Copy</Text>
            </TouchableOpacity>
          </View>
          <Text style={styles.instructionText}>
            Send this data back to the host to complete connection
          </Text>
        </View>
      ) : null}

      {error ? (
        <View style={styles.errorContainer}>
          <Text style={styles.errorText}>{error}</Text>
        </View>
      ) : null}

      {connectionStatus === 'connected' && gameStatus === 'game_over' && onNewGame ? (
        <TouchableOpacity style={styles.newGameButton} onPress={onNewGame}>
          <Text style={styles.newGameButtonText}>New Game</Text>
        </TouchableOpacity>
      ) : null}

      <TouchableOpacity style={styles.disconnectButton} onPress={onDisconnect}>
        <Text style={styles.disconnectButtonText}>Back to Local Game</Text>
      </TouchableOpacity>
    </View>
  );
};

